.box-wrapper {
  margin: 20px 20px 20px 20px;
  align-self: stretch;
  border-radius: 10px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.zp-button {
  border-radius: 20px;
  height: 20px;
  background-color: #3689ff !important;
  .title {
    font-family: SFProDisplay;
    font-size: 11px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.27;
    letter-spacing: normal;
    color: #fcfcfc;
    text-transform: none;
  }
}
.loading-image {
  width: 320px;
  height: 320px;
}

.circle-button-wrapper {
  padding: 10px 10px 10px 10px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 40px 16px 16px 16px;
  position: absolute;
  top: 0px;
  background: transparent;
  .button {
    border-radius: 10px;
    background-color: #fcfcfc;
    width: 20px;
    height: 20px;
  }
  &:active {
    opacity: 0.6;
  }
}
.left-header-button {
  left: 0px;
  opacity: 0.5;

  .button {
    background-color: #f8f8f8;
  }
}
.right-header-button {
  right: 0px;
}
