@mixin icomoon-fonts($url: 'fonts') {
  @font-face {
    font-family: 'zalopay';
    src: url($url+'/zalopay.eot?li1apk');
    src: url($url+'/zalopay.eot?li1apk#iefix') format('embedded-opentype'),
      url($url+'/zalopay.ttf?li1apk') format('truetype'),
      url($url+'/zalopay.woff?li1apk') format('woff'),
      url($url+'/zalopay.svg?li1apk#zalopay') format('svg');
    font-weight: normal;
    font-style: normal;
  }
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'zalopay' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
