html,
body {
  font-size: 16px;
  line-height: 18px;
  color: #24272b;
  height: 100%; }

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none; }

.white-body {
  background-color: #ffffff; }

.dark-body {
  background-color: #f1f4f6; }

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.page-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding-bottom: 80px; }

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #c7c7cc;
  opacity: 1;
  /* Firefox */ }

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #c7c7cc; }

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #c7c7cc; }

